import React from "react"
import {
  DocumentsCollectionAudioItem,
  DocumentsCollectionEventInfo,
} from "@rp/react/tanstack-api/documents"
import { AudioPlayer, AudioPlayerInterface } from "@rp/react/web/common/base-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box } from "@rp/react/web/mui/core"
import { DocumentsCollectionCompactCompanyInfo } from "../DocumentsCollectionCompactCompanyInfo/DocumentsCollectionCompactCompanyInfo"
import { getShouldHideAudioBar } from "./documentsCollectionAudioBar.utils"

type DocumentsCollectionAudioBarProps = AudioPlayerInterface & {
  audioItem: DocumentsCollectionAudioItem | null
  eventInfo: DocumentsCollectionEventInfo | null
}

const DocumentsCollectionAudioBar = ({
  audioItem,
  closeControls,
  currentTime,
  duration,
  isPlaying,
  pause,
  play,
  updateCurrentTime,
  eventInfo,
  areControlsVisible,
  audio,
}: DocumentsCollectionAudioBarProps) => {
  const { isMobile } = useDeviceInfo()
  if (!audio || getShouldHideAudioBar(areControlsVisible, audioItem)) return null

  return (
    <Box
      sx={{
        audio: { width: "100%", height: "25px" },
        margin: isMobile ? "8px" : "12px 16px 12px 222px",
        zIndex: 999,
        position: "fixed",
        bottom: isMobile ? "58px" : "12px",
        left: "0",
        right: "0",
        bgcolor: "#383838",
        display: "flex",
        height: "46px",
        borderRadius: "4px",
        boxShadow: "0px 4px 14px 0px #00000040",
      }}
    >
      <AudioPlayer
        audioItem={audioItem}
        closeControls={closeControls}
        currentTime={currentTime}
        duration={duration}
        isPlaying={isPlaying}
        leftComponent={<DocumentsCollectionCompactCompanyInfo eventInfo={eventInfo} />}
        pause={pause}
        play={play}
        updateCurrentTime={updateCurrentTime}
      />
    </Box>
  )
}

export { DocumentsCollectionAudioBar }
